.page--all-tags {
  padding-top: 12.34rem;
  @include breakpoint(tabletOnly) {
    padding-top: 11.26rem;
  }
  @include breakpoint(desktopOnly) {
    padding-top: 11.6rem;
  }
}

.all-tags {
  width: 100%;
  & .breadcrumbs {
    margin: 0.68rem 0 0 6.3%;
  }
  & .page-title {
    @include marginTop(70.4, 53.7, 59.5);
    @include marginBottom(60, 70, 76);
    @include lineHeight(68, 36, 70);
    @include letterSpacing(0, 0, -25);
    @include fontSize(70, 36, 70);
    text-align: center;
  }
  & .container {
    @include breakpoint(desktopOnly) {
      width: 90%;
      margin-left: 6.3%;
    }
  }
  & .single-tag {
    display: inline-block;
    box-sizing: border-box;
    vertical-align: top;
    @include breakpoint(phoneOnly) {
      width: 52%;
      margin-left: 4%;
      margin-bottom: 8vw;
      &:nth-child(2n+2) {
        width: 40%;
        margin-left: 2%;
      }
    }
    @include breakpoint(tabletOnly) {
      width: 40%;
      margin-bottom: 4.5vw;
      &:nth-child(3n+3) {
        width: 18%;
        margin-right: 0;
      }
    }
    @include breakpoint(desktopOnly) {
      width: 28%;
      margin-bottom: 3.3vw;
      &:nth-child(3n+3) {
        width: 28%;
      }
      &:nth-child(4n+4) {
        width: 14%;
      }
    }
    &__heading {
      font-family: 'Airways';
      font-size: 6.6rem;
      color: $yellow;
      margin-bottom: 2vw;
      @include breakpoint(tabletOnly) {
        font-size: 4.6rem;
        margin-bottom: 2.1vw;
      }
      @include breakpoint(desktopOnly) {
        font-size: 4.6rem;
        margin-bottom: 1.2vw;
      }
    }
    &__link {
      display: block;
      text-align: left;
      color: $black;
      text-decoration: none;
      margin-bottom: 2.2rem;
      font-size: 2.8rem;
      letter-spacing: 0.05em;
      line-height: 1;
      @include breakpoint(tabletOnly) {
        font-size: 1.75rem;
        margin-bottom: 2.5vw;
      }
      @include breakpoint(desktopOnly) {
        font-size: 1.8rem;
        margin-bottom: 0.45vw;
      }
    }
    p {
      padding-top: 0;
      line-height: 1;
    }
  }
  .article-body {
    &__paragraphs {
      & p {
        color: $darkGray;
        padding-top: 0;
        @include fontSize(28, 18, 18);
        @include lineHeight(42, 26, 26);
        @include letterSpacing(50, 50, 50);
        @include marginBottom(41, 22, 30);
      }
      &--heading {
        @include fontSize(34,24,24);
        font-weight: 700;
        text-transform: uppercase;
        margin: 7vw 0 3.7vw 0;
        letter-spacing: 0.05em;
        @include breakpoint(tabletOnly) {
          margin: 5vw 0 3vw 0;
          line-height: 1;
        }
        @include breakpoint(desktopOnly) {
          margin: 1.6vw 0;
          line-height: 1;
        }
        &:first-child {
          margin-top: 0;
          @include marginBottom(10,30,30);
        }
      }
    }
  }
}